import React, { memo } from "react";
import { Map, MapMarker } from "react-kakao-maps-sdk";

const KakaoMap = memo(({ width, height = width, lat, lng }) => {
  return (
    <Map //   Container
      center={{
        //  
        lat: lat ?? 37.3353078277842,
        lng: lng ?? 128.283206354633,
      }}
      style={{
        //  
        width: width ?? "500px",
        height: height ?? "500px",
      }}
      level={3} //   
    >
      <MapMarker //  
        position={{
          //   
          lat: lat ?? 37.3353078277842,
          lng: lng ?? 128.283206354633,
        }}
      />
    </Map>
  );
});

export default KakaoMap;
