import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

/**     */
// payload      
// const 'action' // createAsyncThunk('', ... )
export const petApiList = createAsyncThunk(
  "petApi/list",
  async (payload, { rejectWithValue }) => {
    let result = null;

    try {
      // get('URL)
      result = await axios.get("http://www.pettravel.kr/api/listPart.do?", {
        params: {
          //  
          partCode: payload.partCode ? payload.partCode : "PC02",
          //  
          page: payload.page ? payload.page : 1,
          //   (1~50)
          pageBlock: payload.pageBlock ? payload.pageBlock : 20,
        },
      });
    } catch (e) {
      //   `rejectWithValue()`     extraReducers rejected  .
      result = rejectWithValue(e.response);
    }

    return result;
  }
);

/** Slice (Action + Reducers ) */
const PetApiSlice = createSlice({
  // name:  
  name: "pet",
  initialState: {
    resultList: null, // Ajax    
    totalCount: null,
    loading: false, //  
    error: null, //  
  },
  //  action   action
  reducers: {},
  //  action   action(Ajax)
  extraReducers: {
    [petApiList.pending]: (state, { payload }) => {
      return { ...state, loading: true };
    },
    [petApiList.fulfilled]: (state, { payload }) => {
      return {
        resultList: payload?.data[0].resultList,
        totalCount: payload?.data[0].totalCount,
        loading: false,
        error: null,
      };
    },
    [petApiList.rejected]: (state, { payload }) => {
      return {
        resultList: payload?.data,
        loading: false,
        error: {
          code: payload?.status ? payload.status : 500,
          message: payload?.statusText ? payload.statusText : "Server Error",
        },
      };
    },
  },
});

//   
export default PetApiSlice.reducer;
